*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.user-input{
  margin-top: 1rem;
}
.user-input input[type="text"]{
  padding:.3rem .4rem;
  margin-right: .5rem;
  width: auto;
  border: .1rem solid black;
  border-radius: .3rem;
  font-size: 1.1rem;
}
.user-input input[type="submit"]{
  padding:.3rem .4rem;
  margin-right: .5rem;
  border: .1rem solid black;
  border-radius: .3rem;
  font-size: 1.1rem;
}
.name-tag{
  height: 12rem;
  width: 20rem;
  background-color: red;
  padding: 0rem;
  margin-top: 2rem;
  color: white;
  border-radius: 1.5rem;
  position: relative;
}
.name-tag h2{
  font-size: 3rem;
}
.name-tag p{
  font-weight: 500;
  margin-bottom: .5rem;
}
.name-tag h3{
  background-color: white;
  color: black;
  font-family: cursive;
  font-size: 3rem;
  font-weight: 900;
}
.delete-name{
  position: absolute;
  top: .5rem;
  right: 1rem;
  color: black;
  font-weight: 500;
  font-size: 1.1rem;
  padding:0.01rem .1rem;
}
.delete-name:hover{
  color: gainsboro;
  cursor: pointer;
}